@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.page {
  justify-content: center;
  align-items: center;
  min-width: 100vw;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__element {
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 50%;
  display: flex;
  position: absolute;
}

.stopwatch__element--circle {
  align-items: center;
  height: 100%;
}

.stopwatch__hand-minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch__hand-minutes {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch__hand-seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
}

.stopwatch--speed-up .stopwatch__hand-seconds {
  animation: 10s linear infinite rotate;
}

.stopwatch__centre {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}
